'use client';

import { useEffect, useState } from 'react';

interface Pack {
  id: number;
  name: string;
  description: string;
  files: any[];
}

export default function Dashboard() {
  const [packs, setPacks] = useState<Pack[]>([]);
  const [newPack, setNewPack] = useState({ name: '', description: '' });
  const [errorMessage, setErrorMessage] = useState('');
  const [successMessage, setSuccessMessage] = useState('');

  useEffect(() => {
    fetchPacks();
  }, []);

  const fetchPacks = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('http://127.0.0.1:8000/api/packs', {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Accept': 'application/json',
        },
      });
      const data = await response.json();
      if (data.status === 'success') {
        setPacks(data.data);
      } else {
        setErrorMessage('Erro ao carregar pacotes. Por favor, tente novamente.');
      }
    } catch (error) {
      setErrorMessage('Erro de conexão. Verifique sua internet e tente novamente.');
      console.error('Falha ao buscar pacotes:', error);
    }
  };

  const handleCreatePack = async (e: React.FormEvent) => {
    e.preventDefault();
    setErrorMessage('');
    setSuccessMessage('');

    try {
      const token = localStorage.getItem('token');
      const response = await fetch('http://127.0.0.1:8000/api/packs', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify(newPack),
      });
      const data = await response.json();
      if (data.status === 'success') {
        setNewPack({ name: '', description: '' });
        fetchPacks();
        setSuccessMessage('Pacote criado com sucesso!');
      } else {
        setErrorMessage('Erro ao criar pacote. Verifique os dados e tente novamente.');
      }
    } catch (error) {
      setErrorMessage('Erro de conexão. Verifique sua internet e tente novamente.');
      console.error('Falha ao criar pacote:', error);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white rounded-lg shadow p-6 mb-8">
          <h2 className="text-2xl font-bold mb-4">Criar Novo Pacote</h2>
          <form onSubmit={handleCreatePack} className="space-y-4">
            <div>
              <input
                type="text"
                placeholder="Nome do Pacote"
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-black focus:border-black"
                value={newPack.name}
                onChange={(e) => setNewPack({ ...newPack, name: e.target.value })}
                required
              />
            </div>
            <div>
              <textarea
                placeholder="Descrição"
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-black focus:border-black"
                value={newPack.description}
                onChange={(e) => setNewPack({ ...newPack, description: e.target.value })}
                required
              />
            </div>
            {errorMessage && (
              <p className="text-red-600 text-sm">{errorMessage}</p>
            )}
            {successMessage && (
              <p className="text-green-600 text-sm">{successMessage}</p>
            )}
            <button
              type="submit"
              className="w-full bg-black text-white py-2 px-4 rounded-md hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-black"
            >
              Criar Pacote
            </button>
          </form>
        </div>

        <div className="bg-white rounded-lg shadow">
          <div className="px-6 py-4 border-b border-gray-200">
            <h2 className="text-2xl font-bold">Seus Pacotes</h2>
          </div>
          <div className="divide-y divide-gray-200">
            {packs.length === 0 ? (
              <div className="p-6 text-gray-500">
                Nenhum pacote encontrado. Crie seu primeiro pacote acima.
              </div>
            ) : (
              packs.map((pack) => (
                <div key={pack.id} className="p-6">
                  <h3 className="text-lg font-medium">{pack.name}</h3>
                  <p className="mt-1 text-gray-600">{pack.description}</p>
                  <p className="mt-2 text-sm text-gray-500">
                    {pack.files.length} {pack.files.length === 1 ? 'arquivo' : 'arquivos'}
                  </p>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
